<template>
  <div class="container">
    <div class="btns">
      <p @click="cancel">取消</p>
      <p @click="publish">发布</p>
    </div>
    <van-field
      v-model="message"
      rows="3"
      autosize
      type="textarea"
      maxlength="200"
      placeholder="写下此刻的想法..."
      show-word-limit
      @focus="handleFocus"
    />
    <van-uploader
      v-model="fileList"
      multiple
      :max-count="9"
      :before-delete="handleDelete"
    />
    <van-popup v-model="show" position="bottom" :style="{ height: '29%' }">
      <softKeyBoard
        @onChange="onChange"
        type="lowercase"
        :inputText="message"
      />
    </van-popup>
    <!-- <tabbar activeIndex="publish" /> -->
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      show: false,
      message: "",
      fileList: [
        { url: "https://img01.yzcdn.cn/vant/leaf.jpg" },
        // Uploader 根据文件后缀来判断是否为图片文件
        // 如果图片 URL 中不包含类型信息，可以添加 isImage 标记来声明
        { url: "https://img01.yzcdn.cn/vant/sand.jpg", isImage: true },
      ],
    };
  },
  methods: {
    handleFocus() {
      this.show = true;
    },
    onChange(input) {
      this.message = input;
    },
    cancel() {
      this.$router.back();
    },
    publish() {
      console.log(this.fileList.length);
      if (this.fileList.length > 0 || this.message) {
        Toast.success("发布成功");
        this.$router.back();
      } else {
        Toast("请输入文字或上传图片视频");
      }
    },
    // 第一个参数为地址，第二个参数是下标
    handleDelete(url, index) {
      this.fileList.splice(index, 1);
    },
  },
};
</script>

<style scoped>
.container {
  /* margin: 10px 20px; */
  height: calc(100vh - 50px);
}
.btns {
  font-family: "PingFang SC";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.btns p:first-child {
  color: rgba(0, 0, 0, 0.4);
}
.btns p:last-child {
  color: #6e86ff;
}
>>> .van-cell::after {
  border: none;
}
>>> .van-uploader__preview {
  margin: 0 5px 5px 0;
}
>>> .van-field {
  margin-bottom: 5px;
}
>>> .van-overlay {
  background: rgba(0, 0, 0, 0);
}
</style>